<template>
    <el-dialog v-dialogDrag :title="dialogTitle" :visible.sync="visible" @close="closeDialog(false)">
        <el-form size="mini" :inline="true">
            <el-form-item label="">
                <el-input v-model="dialogData.userName" placeholder="用户名" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">用户名</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.nickName" placeholder="真实姓名" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">真实姓名</template>
                </el-input>
            </el-form-item>
            <el-form-item label="性别" label-width="80px">
                <el-radio v-model="dialogData.is_Male" :label="1">男</el-radio>
                <el-radio v-model="dialogData.is_Male" :label="0">女</el-radio>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.phoneNum" placeholder="手机号码" :maxlength="20" class="inputwinth192">
                    <template slot="prepend">手机号码</template>
                </el-input>
            </el-form-item>
            <el-form-item label="状态" label-width="80px">
                <el-radio v-model="dialogData.is_Enable" :label="1">启用</el-radio>
                <el-radio v-model="dialogData.is_Enable" :label="0">弃用</el-radio>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" icon="fa fa-file-o" @click="closeDialog(false)"> 取 消</el-button>
            <el-button size="mini" icon="fa fa-file-text-o" type="primary" @click="saveDialog"> 保 存</el-button>
        </div>
    </el-dialog>
</template>
 
<script>
export default {
    data() {
        return {
            moduleName: "userInfo",
            visible: this.dialogShow,
            roleList: [],
            organizeList: [],
            organizeTree: []
        };
    },
    props: {
        dialogTitle: "",
        dialogShow: { Boolean: false },
        dialogData: {
            id: 0,
            code: "",
            name: "",
            organizeId: "",
            organizeIds: "",
            organizeList: [],
            is_Enable: 1,
            is_Remove: 0
        }
    },
    created() {
        //this.initData();
    },
    methods: {
        initData() {
            this.$ajax.get(this.moduleName + "/findResource").then(res => {
                this.roleList = res.data.roleList;
                this.organizeList = res.data.organizeList;
                this.organizeTree = res.data.organizeTree;
            });
        },
        closeDialog(isclose) {
            this.$emit("update:dialogShow", isclose);
        },
        saveDialog() {
            // this.dialogData.organizeId = this.dialogData.organizeList[
            //     this.dialogData.organizeList.length - 1
            // ];
            // this.dialogData.organizeIds = this.dialogData.organizeList.join();
            var url =
                this.moduleName +
                (this.dialogData.id > 0 ? "/updateOne" : "/insertOne");
            this.$ajax.post(url, this.dialogData).then(res => {
                this.$emit("update:dialogData", res.data);
                this.$emit("update:dialogShow", false);
                this.$emit("RefreshData");
            });
        }
    },
    watch: {
        dialogShow() {
            this.visible = this.dialogShow;
        }
    }
};
</script>



